@use "global" as *;

$drawer-width: rem(24); //ドロワーの幅
$drawer-height: rem(2); //ドロワー線の高さ
$drawer-color: #fff; //ドロワー線の色

.hamburger {
 z-index: map-get($layer, 'hamburger');
}

.hamburger span {
 position: relative;
 display: block;
 height: $drawer-height;
 width: $drawer-width;
 background: $drawer-color;
 transition: ease .3s;
}

.hamburger span:nth-child(1) {
 top: 0;
}

.hamburger span:nth-child(2) {//真ん中の線で上下の間隔を設定
 margin: rem(4) 0;
}

.hamburger span:nth-child(3) {
 top: 0;
}
/*is-OPEN時の動き*/
.hamburger.is-open span:nth-child(1) {
 top: rem(5);
 transform: rotate(45deg);
//  transform: rotate(225deg);//180度+45度にすると結構回って見えたり角度を変えて遊べる
}

.hamburger.is-open span:nth-child(2) {
 transform: translateY(-50%);
 opacity: 0;//消す
}

.hamburger.is-open span:nth-child(3) {
 top: rem(-7);
 transform: rotate(-45deg);
}
